@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Space+Grotesk:wght@700&display=swap');
*,*::after,*::before{
box-sizing: border-box;
}
body{
    display: flex;
    justify-content: center;
 
    background:linear-gradient(70deg ,rgb(210, 124, 139),rgb(39, 172, 216));
}
.calc_grid{
    display: grid;
    margin-top: 1rem;
    grid-template-columns: repeat(4,6rem);
    grid-template-rows:minmax(7rem,auto) repeat(5,6rem);

}
.span2{
    grid-column: span 2;
}
.output{
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    grid-column: 1/-1;
    background-color: rgba(0, 0, 0, 0.651);
    display: flex;
    flex-direction: column;
    align-items: end;
    padding: 5px;
    word-wrap: break-word;
    word-break: break-all;
}
.prev_op{
    color: grey;
    font-size: 19px;
    margin: 5px;
}
.curr_op{
    color: rgb(255, 255, 255);
    font-size: 35px;
    margin: 5px;
}

button{
    cursor: pointer;
    font-size: 28px;
    background-color: rgba(255, 255, 255, 0.601);
    outline: none;
    border: none;
    margin: 1.2px;
    transition: all ease-in-out .1s;
    font-family: 'Abril Fatface', cursive;
}
button:active{
    font-size: 42px;
}
button:hover{
    background-color: transparent;
}
.lastzero{
    background-color: red;
}
.lasta{
    border-bottom-right-radius: 20px;

}